<script>
import { mapState } from "vuex";
import { mediaUrl, appUrl } from "../../../core/constant/data";
// import imageCompression from "browser-image-compression";
// import axios from "../../../core/axios";
export default {
  data() {
    return {
      storeData: {
        cloud_name: "",
        api_key: "",
        upload_preset: "",
      },
      mediaUrl: mediaUrl,
      appUrl: appUrl,
      activeName: "media",
      selected: [],
      loading: false,
      active: false,
      index: null,
      lightBoxFiles: [],
      saving: false,
      media_key: {
        cloud_name: "",
        api_key: "",
        upload_preset: "",
      },
    };
  },
  computed: {
    ...mapState(["files", "isReady", "store"]),
  },
  watch: {
    file(val) {
      if (val) {
        this.file.forEach((image) => {
          image.isSelected = false;
          this.lightBoxFiles.push(this.mediaUrl + image.image);
        });
        this.$store.commit("setIsReady", true);
      }
    },
    store(val) {
      this.media_key = JSON.parse(val.fbLink);
    },
  },
  mounted() {
    this.$store.commit("setIsReady", false);
    if (this.store) {
      this.media_key = JSON.parse(this.store.fbLink);
    }
    this.$store.commit("setIsReady", true);
    if (this.files) {
      this.files.forEach((image) => {
        image.isSelected = false;
        this.lightBoxFiles.push(this.mediaUrl + image.image);
      });
    }
  },
  methods: {
    changeSelect() {
      this.selected = this.files.filter((image) => {
        return image.isSelected;
      });
    },
    Post() {
      this.$smalltalk
        .confirm(
          "Question",
          "Are you sure you want to update cloudinary detail?"
        )
        .then(() => {
          this.saving = true;
          var store = this.store;
          store.fbLink = JSON.stringify(this.media_key);
          this.$axios.put("store/", { data: store }).then((response) => {
            this.$store.commit("setStore", response.data);
            this.saving = false;
            this.$noti.display({
              color: "success",
              title: "Success",
              message: "Cool! Store has been successfully updated.",
            });
          });
        });
    },
    setHeight(e) {
      e.path[0].style.height = e.path[0].clientWidth + "px";
    },

    async onFileChange(e) {
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        alert("The file uploaded need to be -jpeg,jpg,png");
        return false;
      }
      if (e.target.files[0].size / 1000000 > 1) {
        alert("Max image file size is 1MB!");
        return false;
      }
      this.loading = true;
      var file = e.target.files[0];
      const formData = new FormData();
      formData.append("file", file);
      formData.append("upload_preset", this.media_key.upload_preset);
      formData.append("api_key", this.media_key.api_key);
      formData.append("timestamp", (Date.now() / 1000) | 0);

      var response = await fetch(
        `https://api.cloudinary.com/v1_1/${this.media_key.cloud_name}/image/upload`,
        {
          method: "POST",
          body: formData,
        }
      );
      const img = await response.json();
      this.uploadImageToServer({
        image: img.secure_url,
      });
    },
    uploadImageToServer(img) {
      this.$axios
        .post("files/", { data: img })
        .then(() => {
          this.$store.commit("addFile", [img]);
          this.activeName = "media";
          this.loading = false;
        })
        .catch((err) => {
          this.loading = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    removeFiles() {
      if (this.selected.length > 0) {
        this.loading = true;
        var data = [];
        this.selected.forEach((image) => {
          data.push(image.id);
        });
        this.$axios
          .post("remove-files/", { data })
          .then(() => {
            this.$store.commit("removeFile", data);
            this.selected = [];
            this.loading = false;
            this.$noti.display({
              message: "Cool! File has been successfully deleted.",
              title: "success",
              color: "success",
            });
          })
          .catch((err) => {
            this.$noti.display({
              message: err,
              status: "error",
              color: "error",
            });
            this.loading = false;
          });
      }
    },
  },
};
</script>

